body
{
    font-family: 'Montserrat';
}
header
{
    background-color: #272727;
}
.header-heading
{
    font-family: 'Montserrat Light';
    font-size: 45px;
    font-weight: 300;
    /* Text style for "Services" */

    letter-spacing: -.06px;

    color: #303439;
}
.nav-item
{
    font-family: 'Montserrat';
    font-size: 24px;
    font-weight: 400;
    font-style: normal;

    letter-spacing: -.03px;
    text-transform: lowercase;
}
.navbar-brand
{
    font-family: 'Montserrat';
    font-size: 24px;
    font-weight: 400;

    margin-right: 0;

    letter-spacing: -.3px;
    text-transform: lowercase;
}
.all-projects
{
    font-family: 'Montserrat';
    font-size: 24px;
    font-weight: 400;

    letter-spacing: -.03px;

    color: #303439;
}
.button-rounder
{
    height: 43px;

    cursor: pointer;
    width: 222px;

    border: 1px solid rgba(32, 32, 32, .5);
    border-radius: 22px;
}
.panda-block
{
    background: url('../img/panda.png') no-repeat center center;
    background-size: cover;
}

.fragmanu-block
{
    background: url('../img/fragmanu.png') no-repeat center center;
    background-size: cover;
}

.client-block
{
    background: url('../img/Cucina.png') no-repeat center center;
    background-size: cover;
}

.footer-text
{
    font-family: 'Montserrat Light';
    font-size: 18px;
    font-weight: 300;

    letter-spacing: -.03px;

    opacity: .5;
    color: #303439;
}

.contact-text
{
    font-family: 'Montserrat';
    font-size: 30px;
    font-weight: 400;

    letter-spacing: -.4px;

    color: #303439;
}
.contact-title
{
    font-family: 'Montserrat';
    font-size: 18px;
    font-weight: 400;

    letter-spacing: -.03px;
    text-transform: uppercase;

    color: #798ea5;
}
.list-text
{
    font-family: 'Montserrat Light';
    font-size: 18px;
    font-weight: 300;

    letter-spacing: -.3px;

    color: #303439;
}

.padding-block
{
    padding: 155px;
    padding-left: 75px;
}
